import React from 'react'
import GoogleMapReact from 'google-map-react'

import google_map_icon from "../images/google_maps.png"

const defaultProps = {
  center: {
    lat: 51.49916785442426,
    lng: -0.1160955168977971,
    icon: google_map_icon,
  },
  zoom: 15,
}

const AnyReactComponent = ({ text }) => <div>{text}</div>

const GoogleMap = () => (
  <div style={{ height: '400px', width: '100%' }}>
    <GoogleMapReact
      // bootstrapURLKeys={{ key: 'AlzaSyBRKoTpCZsUXfcXyxHxoK-PpXMHYwfqs8o' }}
      bootstrapURLKeys={{ key: 'AIzaSyDOvtlMs5GcsNTMwVykwNfwlBEtvYb4HwI' }}
      defaultCenter={defaultProps.center}
      defaultZoom={defaultProps.zoom}
    >
      <AnyReactComponent
        lat={51.49916785442426}
        lng={-0.1160955168977971}
        text={'PsycApps Ltd'}
        icon={google_map_icon}
      />
    </GoogleMapReact>
  </div>
)

export default GoogleMap
